<style lang="less">
@import "./login.less";
</style>

<template>
  <div class="login">
    <div class="login-con">
      <Card icon="log-in" title="欢迎登录" :bordered="false">
        <div class="form-con">
          <login-form @on-success-valid="handleSubmit"></login-form>
          <p class="login-tip">输入任意用户名和密码即可</p>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import LoginForm from "../components/login-form/index";
import qs from "qs";
export default {
  components: {
    LoginForm
  },
  methods: {
    handleSubmit({ userName, password }) {
      let params = {
        username: userName,
        password: password
      };
      // console.log(params);
      this.$axios.post("/app/sys/login", qs.stringify(params)).then(res => {
        if (res.status == 200) {
          if (res.data) {
            this.$Message.success("登陆成功");
            this.$router.push({ name: "About" });
          } else {
            this.$Message.error("登陆失败");
          }
        }
      });
    }
  }
};
</script>

